@import "_header";
@import "_footer";
.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.container {
    max-width: 1300px;
    margin: 0 auto;
    padding: 0 50px;
    @media screen and (max-width: 992px) {
        padding: 0 24px;
    }
}